import {
    Component,
    h,
    Prop,
    Event,
    EventEmitter,
    Host,
    State,
    Element,
    Watch,
} from '@stencil/core';
import { ColDef } from 'ag-grid-community';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Data Tables
 * @description A menu component for filtering table data. Provides a user-friendly interface for filtering data from tables.
 */

@Component({
    tag: 'atui-table-filter-menu',
    shadow: false,
})
export class AtuiTableFilterMenu {
    /**
     * Column definitions used in your atui-table
     */
    @Prop()
    col_defs: ColDef[];

    /**
     * Currently selected filter columns
     */
    @Prop({ mutable: true })
    selected: string[] = [];

    @State() translations: any;
    @Element() el: HTMLAtuiTableFilterMenuElement;

    /**
     * Emits selected columns when checkbox selection changes
     */
    @Event() atuiChange: EventEmitter<string[]>;

    private get filteredColumns() {
        return this.col_defs
            .filter((colDef) => colDef.field !== 'Checkbox')
            .map((colDef) => ({
                option_id: colDef.field,
                label: colDef.field,
            }));
    }

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    @Watch('selected')
    handleSelectedChange(newValue: string[]) {
        this.selected = newValue || [];
    }

    private handleColumnSelect(event: CustomEvent<string[]>): void {
        const newValue = event.detail || [];
        this.selected = newValue;
        this.atuiChange.emit(this.selected);
    }

    render() {
        return (
            <Host>
                <atui-menu
                    autoclose={false}
                    width="fit-content"
                    class="self-start"
                >
                    <atui-tooltip
                        slot="menu-trigger"
                        position="top"
                    >
                        <atui-button
                            slot="tooltip-trigger"
                            type="secondaryOutline"
                            icon="filter_list"
                        />
                        <span>{this.translations.ATUI.TABLE.FILTER_DATA}</span>
                    </atui-tooltip>
                    <div class="flex flex-col">
                        <atui-checkbox-group
                            class="w-fit"
                            options={this.filteredColumns}
                            value={this.selected}
                            onAtuiChange={(event: CustomEvent<string[]>) =>
                                this.handleColumnSelect(event)
                            }
                        />
                    </div>
                </atui-menu>
            </Host>
        );
    }
}
